








































































  import { Component, Vue } from 'vue-property-decorator'
  import { mixins } from 'vue-class-component'
  import { axiosProxy, checkErrorCode, showErrorMessage } from '@/assets/js/public'
  import { Status, Fee } from '@/assets/js/variables'
  import Functions from './methods/content'
  import { Button, List, PullRefresh, Card, Cell, Tag, Picker, Search, Divider } from 'vant'
  Vue.use(Button)
  Vue.use(List)
  Vue.use(PullRefresh)
  Vue.use(Card)
  Vue.use(Cell)
  Vue.use(Tag)
  Vue.use(Picker)
  Vue.use(Search)
  Vue.use(Divider)

  interface Visible {
      showPicker: boolean,
      showPop: boolean
  }

  @Component({
      mixins: [Functions]
  })
  export default class cmsHeader extends Vue {
      loading: boolean = false
      finished: boolean = false
      refreshing: boolean = false
      columns: string[] = []
      selections: any[] = [{
          values: [],
          defaultIndex: 1,
          searchType: 'productType'
      }, {
          values: [],
          defaultIndex: 4,
          searchType: 'status'
      }, {
          values: [],
          defaultIndex: 0,
          searchType: 'modelId'
      }]
      apiMap: any = null
      modelMap: any = null
      currentAction: string = ''
      visibleObj: Visible = {
          showPicker: false,
          showPop: false
      }

      mounted () {
          this.getApiList()
          this.getModelList()
          this.selections[1].values = Array.from(new Status(null).basicMap.keys())
          this.selections[0].values = Array.from(new Fee(null).basicMap.keys())
          // todo 树组件
	    }

      doQuery (data, val) {
          this['queryParams']['status'] = new Status(null).getVal(data[1])
          this['queryParams']['productType'] = new Fee(null).getVal(data[0])
          this['queryParams']['modelId'] = this.modelMap.get(data[2])
          this['tableData'] = []
          this['pageObj']['page'] = 1
          this['getTableData'](this['pageObj']['page'], this['pageObj']['pageSize'], 'productType=' + this['queryParams']['productType'], 'modelId=' + this['queryParams']['modelId'], 'status=' + this['queryParams']['status']).then(() => {
              this.visibleObj.showPop = false
          })
      }

      onConfirm (data) {
          let asyncId = this.apiMap.get(data)
          let siteId = window.localStorage.getItem('_site_id_param')
          let url = 'http://cmstest.cnlive.com:8083/job/getcontent' + '/' + siteId
          if(this.apiMap.get(data) !== -1) {
              url += '/' + asyncId
          }
          showErrorMessage('操作成功', null)
          axiosProxy.get(url).then( res => {
              this['tableData'] = []
              this['pageObj']['page'] = 0
              this.onLoad()
              this.visibleObj.showPicker = false
          }).catch(error => {
              console.warn(error)
          })
      }

      getApiList () {
          // @ts-ignore
          let url = this.$collectUrl('syncApiList')
          axiosProxy.get(url).then( res => {
              checkErrorCode(res).then(respond => {
                  let apiList: any[] = res.data.data
                  this.apiMap = new Map<string, number>()
                  this.apiMap.set('全部', -1)
                  apiList.forEach((item, index) => {
                      item.enable == false? apiList.splice(index, 1): void 0
                      this.apiMap.set(item.showName, item.id)
                  })
                  this.columns = Array.from(this.apiMap.keys())
              })
          }).catch ( error => {
              console.warn(error)
          })
      }

      getModelList () {
          // @ts-ignore
          let url = this.$collectUrl('modelList') + '?enable=true'
          axiosProxy.get(url).then(res => {
              checkErrorCode(res).then(respond => {
                  let modelList: any[] = res.data.data
                  window.localStorage.setItem('modelList', JSON.stringify(modelList))
                  this.modelMap = new Map<string, number>()
                  this.modelMap.set('所有模型', -1)
                  modelList.forEach((item, index) => {
                      this.modelMap.set(item.showName, item.id)
                  })
                  this.selections[2].values = Array.from(this.modelMap.keys())
              }).catch(error => {
                  console.warn(error)
              })
          })
      }

      switchActionType (actionType: string, dataObj: any) {
          interface Actions {
              'group'(): void,
              'more'(): void,
              'top'(): void,
              'async'(): void,
              'search'(): void,
              'channel'(): void
          }

          let actionObj: Actions = {
              'group': () => {
                  console.log('去关联群组')
              },
              'async': () => {
                  console.log('开始选择')
                  this.visibleObj.showPicker = true
              },
              'search': () => {
                  console.log('筛选')
                  this.visibleObj.showPop = true
              },
              'channel': () => {
                  console.log('选择栏目')
              },
              'top': () => {
                  console.log('置顶和取消置顶')
                  let isTop, id = dataObj.id
                  dataObj.topLevel? isTop = false: isTop = true
                  // @ts-ignore
                  let url = this.$collectUrl('contentTop')
                  axiosProxy.put(url + '?id=' + id + '&isTop=' + isTop).then(res => {
                      checkErrorCode(res).then(respond => {
                          showErrorMessage('操作成功', null)
                          this['tableData'] = []
                          this['pageObj']['page'] = 0
                          this.onLoad()
                      })
                  }).catch(error => {
                      console.warn(error)
                  })
              },
              'more': () => {
                  console.log('其他操作')
              }
          }
          if(typeof(actionObj[actionType]) !== 'function') {
              return false
          } else {
              this.currentAction = actionType
              return actionObj[actionType]
          }
      }

      moreAction (obj, type) {
          this.switchActionType(type, obj)()
      }

      onLoad() {
          if (this.refreshing) {
              this.refreshing = false
          }

          this['pageObj']['page'] += 1
          this['getTableData'](this['pageObj']['page'], this['pageObj']['pageSize'], 'productType=' + this['queryParams']['productType'], 'modelId=' + this['queryParams']['modelId'], 'status=' + this['queryParams']['status']).then(() => {

          })
          // this['getTableData'](this['pageObj']['page'], this['pageObj']['pageSize'])
          this.loading = false

          if(this['pageObj']['page'] == this['pageObj']['totalPages']) {
              this.finished = true
          }
      }

      onRefresh() {
          this.finished = false
          this.loading = true
          this['pageObj']['page'] = 0
          this['tableData'].length = 0
          this.onLoad()
      }
  }
