// 内容上线状态
class Status {
    basicMap: any
    constructor(basicMap: any = null) {
        this.basicMap = basicMap = new Map<string, string>()
        basicMap.set('所有状态', '-1')
        basicMap.set('待编辑', '0')
        basicMap.set('草稿', '1')
        basicMap.set('审核拒绝', '4')
        basicMap.set('已上线', '5')
        basicMap.set('已下线', '6')
        basicMap.set('删除', '7')
        basicMap.set('禁用', '8')
        basicMap.set('收费', '10')
    }

    getVal (key: string): string {
        let val: string = this.basicMap.get(key)
        return val
    }

    getKey (val: string): string {
        let key: string = ''
        this.basicMap.forEach((sKey, sVal) => {
            if(val == sKey) {
                key = sVal
            }
        })
        return key
    }
}

