import { Component, Vue } from 'vue-property-decorator'

import { axiosProxy, checkErrorCode, showErrorMessage, timestampToTime } from '@/assets/js/public'

interface Page {
    allTotal: number,
    totalPages: number,
    curPage: number,
    pageSize: number,
    page: number
}

interface Query {
    title: string,
    spId: string,
    id: string,
    productType: string,
    modelId: string,
    status: string
}

@Component({

})

export default class cmsHeader extends Vue {
    pageObj: Page = {
        allTotal: 0,
        totalPages: 0,
        curPage: 1,
        pageSize: 10,
        page: 0,
    }
    queryParams: Query = {
        title: '',
        spId: '',
        id: '',
        productType: '',
        modelId: '',
        status: ''
    }
    tableData: any[] = []

    mounted () {

    }

    onSearch (searchType) {
        this['tableData'] = []
        this['pageObj']['page'] = 0
        this.getTableData(1, 10, searchType + '=' + this['queryParams'][searchType])
        this['visibleObj']['showPop'] = false
    }

    getTableData (page: number = 1, pageSize: number = 10, ...args) {
        // @ts-ignore
        let url = this.$collectUrl('contentList') + '?page=' + page + '&pageSize=' + pageSize
        let argums = [...args]
        argums.forEach((item, index) => {
            let endingStr = item.charAt(item.length - 1)
            let endingParam = item.substring(item.length - 2)
            if(Number(endingParam) !== -1 && endingStr !== '=') {
                url += '&' + item
            }
        })
        let p = new Promise((resolve, reject) => {
            axiosProxy.get(url).then(res => {
                checkErrorCode(res).then(respond => {
                    this['tableData'] = [...this['tableData'], ...res.data.data.list]
                    this['pageObj']['allTotal'] = res.data.data.total
                    this['pageObj']['totalPages'] = res.data.data.pages
                    this['pageObj']['curPage'] = res.data.data.pageNum
                    this['tableData'].map((item, index) => {
                        item.publishTime? item.publishTime = timestampToTime(item.publishTime): null

                        let feeMap = new Map <number, string> ()
                        feeMap.set(0, '免费')
                        feeMap.set(1, '积分')
                        feeMap.set(2, 'RMB')
                        feeMap.set(4, '试听')
                        feeMap.set(5, '专辑')
                        feeMap.set(6, '兑码')
                        item.feeType = feeMap.get(parseInt(item.productType))? feeMap.get(parseInt(item.productType)): '免费'
                    })
                    resolve()
                })
            }).catch(error => {
                console.warn(error)
            })
        })
        return p
    }
}
